<script>
  import './_global.pcss';
  import { theme } from './store';
  import Nav from '$components/Nav.svelte';
  import { onMount } from 'svelte';

  onMount(() => {
    if ($theme === 'dark') {
      document.querySelector('html').classList.add('dark');
    }
    console.log('themes ', $theme);
  });
</script>


